<script>
import drawer from "@/components/drawer.vue";
import { mapMutations, mapState } from "vuex";
export default {
  name: "pageTwo",

  data() {
    return {
      listsData: [],
      videoInfo: ""
    };
  },
  components: {
    drawer
  },
  computed: {
    ...mapState(["videoStatus"])
  },
  methods: {
    ...mapMutations(["changeVideoStatus"]),
    showFn(id) {
      this.videoInfo = id;
      //   this.videoInfo.status = this.changeVideoStatus(true);
      this.changeVideoStatus(true);
      //   this.$store.commit("changeVideoStatus", true);
    }
  },
  mounted() {
    console.log("444:", process.env);
    this.http
      .get(process.env.VUE_APP_BASE_URL + "data/secondPage.json")
      .then(res => {
        this.listsData = res.data.lists;
      });
  }
};
</script>
