<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png">
    <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <div v-show="!showPage" class="phoneBox">
      <p>{{ introShow }}</p>
    </div>
    <full-page
      ref="fullpage"
      :options="options"
      id="fullpage"
      v-show="showPage"
    >
      <div class="section">
        <pageOne />
      </div>
      <div class="section">
        <pageTwo />
      </div>
    </full-page>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import pageOne from "@/views/pageOne.vue";
import pageTwo from "@/views/pageTwo.vue";
export default {
  name: "Home",
  data() {
    return {
      intro: "",
      introShow: "",
      num: 0,
      timer: null,
      showPage: true,
      options: {
        scrollOverflow: true
        // continuousVertical: true //循环
      }
    };
  },
  components: {
    pageOne,
    pageTwo
    // HelloWorld
  },
  methods: {
    showIntro() {
      let len = this.intro.length;
      if (this.num <= len) {
        this.introShow = this.intro.slice(0, this.num++) + "_";
        this.timer = setTimeout(this.showIntro, 150);
      } else {
        clearTimeout(this.timer);
        //使用replace需要一个变量接收，否则不会有效果
        this.introShow = this.introShow.replace("_", "");
      }
    }
  },
  mounted() {
    if (document.body.clientWidth < 1100) {
      this.showPage = false;
      this.http
        .get(process.env.VUE_APP_BASE_URL + "data/firstPage.json")
        .then(res => {
          this.intro = res.data.phone;
          this.showIntro();
        });
    }
  }
};
</script>
