<template>
  <div class="navBar">
    <span class="logo"><img src="@/assets/logo.jpeg" alt=""/></span>
    <p class="title">dorryLiu 2020</p>
    <div class="erCode">
      <span class="font" @click="showErcode">点击查看二维码({{ time }}s)</span>
      <p class="code" v-show="ercode">
        <img src="@/assets/ercode.png" alt="" />
      </p>
    </div>
  </div>
</template>
<script>
export default {
  name: "navBar",
  data() {
    return {
      ercode: false,
      time: 5
    };
  },
  methods: {
    showErcode(time) {
      this.ercode = true;
      let timer = null;
      if (this.time > 0) {
        timer = setTimeout(() => {
          this.time--;
          console.log(333);
          this.showErcode(this.time);
        }, 1000);
      } else {
        this.ercode = false;
        timer = clearTimeout(timer);
        this.time = 5;
      }
    }
  }
};
</script>
