<template>
  <div class="pageOneBox">
    <navBar />
    <div class="pageOne">
      <p class="info">{{ introShow }}</p>
    </div>
  </div>
</template>
<script>
import navBar from "@/components/navBar";
export default {
  name: "pageOne",
  data() {
    return {
      intro: "",
      introShow: "",
      num: 0,
      timer: null
    };
  },
  components: {
    navBar
  },
  methods: {
    showIntro() {
      let len = this.intro.length;
      if (this.num <= len) {
        this.introShow = this.intro.slice(0, this.num++) + "_";
        this.timer = setTimeout(this.showIntro, 150);
      } else {
        clearTimeout(this.timer);
        //使用replace需要一个变量接收，否则不会有效果
        this.introShow = this.introShow.replace("_", "");
      }
    }
  },
  mounted() {
    console.log("222:", process.env);
    this.http
      .get(process.env.VUE_APP_BASE_URL + "data/firstPage.json")
      .then(res => {
        this.intro = res.data.intro;
        this.showIntro();
      });
  }
};
</script>
