import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    videoStatus: false
  },
  mutations: {
    changeVideoStatus(state, status) {
      state.videoStatus = status;

      console.log('status:', status)
    }
  },
  actions: {},
  modules: {}
})